import React from 'react';
import './course.css';
import { Link } from 'react-router-dom';

const Course = ({course}) => {
    const rateStars = [];
    for (let i = 0; i < course.rating; i++) {
        rateStars.push(<i key={i} className="ion-android-star rating-star" aria-hidden="true"></i>);
    }
    return (
        <div className="col-sm-6 col-md-3">
            <div className="courses_item__wrapper">
                <div className="courses__item">
                    <div className="card__top">
                        <p className="card__nbr">{course.number}.</p>
                        <div className={`courses__icon ${course.iconClass}`}><i className="ion-ios-timer-outline"></i></div>
                        <div className="courses__title">{course.title}</div>
                        <p className="courses__desc">{course.description}</p>
                    </div>
                    <div className="card__back">
                        <div className="card_back__wrapper">
                            <div className="courses__title">{course.title}</div>
                            <ul className="course__info">
                                <li><i className="ion-calendar course-calendar" aria-hidden="true"></i>{course.period}</li>
                                <li>Рейтинг:
                                    { rateStars }
                                </li>
                                <li>Стоимость: <span>{course.price}</span></li>
                                <li>Преподаватель: <span>{course.teacher}</span></li>

                            </ul>
                            <Link to={course.courseLink} className="btn btn-primary text-center">Описание</Link>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default Course;